body {
  background-color: #777e85;
}

.card {
  box-shadow: 0 0 5px 0;
  /* background: inherit; */
  backdrop-filter: blur(10px);
}

.grid {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  grid-gap: 15px;
}

.box {
  width: auto;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icons {
  font-size: 35px;
  color: #111010;
}

.button {
  width: 100%;
}
